/**
 * The actual naming of elements is not rigid, these are just examples
 */

/* Zoom parent */
.zoom-container {
  position: relative;
}

/* Image to zoom */
#zoom-target {
  position: relative;

  /* Not strictly necessary but useful for positioning the loading message */
  display: block;

  /* This is very important, the element must have metrics to provide an area to catch events */
}

#zoom-target .fullsize-loading {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 40px;
  height: 40px;
  margin: -20px 0 0 -20px;
  direction: ltr;
  text-indent: -9999px;
  background: #fff url(../img/loading.gif) no-repeat center center;
  z-index: 150;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}

#zoom-panel {
  display: none;
}

.zoom-instructions {
  display: none;
}

@media (min-width: 768px) {
  /* Zoom panel */
  #zoom-panel {
    display: block;
    position: fixed;
    top: 20px;
    right: 20px;
    width: 55%;
    height: 420px;
    overflow: hidden;
    background: white;
    z-index: 200;

    /* Improve fluid motion of the the panel */
    -webkit-transition: left, top 100ms ease-out;
    -moz-transition: left, top 100ms ease-out;
    -ms-transition: left, top 100ms ease-out;
    -o-transition: left, top 100ms ease-out;
    transition: left, top 100ms ease-out;
  }

  .zoom-panel img {
    padding: 0px;
  }

  .zoom-instructions {
    color: #898989;
    float: right;
    width: 50%;
    display: block;
    text-align: center;
    border: 2px dashed #898989;
    height: 400px;
  }
}

/* Zoom gallery */
.zoom-gallery {
  margin: 15px 0;
  list-style-type: none;
}

.zoom-thumbnail {
  float: left;
  margin-left: 15px;
  text-decoration: none;
}

